.title {
  font-family: "Caveat", cursive;
  font-size: 64px;
  color: white;
}
.titleWrapper {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: rgb(88, 88, 88);
  width: 40vw;
  height: 10vh;
}
.wrapper {
  position: sticky;
  z-index: 1;
  top: 0;
  background-color: black;
}
.page {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 80vh;
}
.about {
  display: flex;
  justify-content: center;
  font-size: 48px;
  font-weight: bold;
  padding-top: 10vh;
  padding-bottom: 10vh;
}
p {
  color: white;
  font-size: large;
  margin: 0px 80px 0px 80px;
  font-family: system-ui, -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto,
    Oxygen, Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
}
.project {
  display: flex;
  justify-content: center;
  font-size: 48px;
  font-weight: bold;
  padding-top: 10vh;
  padding-bottom: 10vh;
}
.projectList {
  color: white;
  display: grid;
  grid-gap: 100px;
  justify-content: center;
  grid-template-columns: 1fr 1fr;
  height: 75vh;
}
.project1,
.project2,
.project3,
.project4 {
  display: flex;
  justify-content: center;
  align-items: center;
}
.arrow {
  z-index: 2;
  top: 65%;
  transform: translate(-50%, -50%);
  transform: rotate(0deg);
  cursor: pointer;
}
a {
  text-decoration: none;
}
a:hover {
  border-bottom: 2px solid orange;
}
a:visited {
  color: inherit;
}
a:active,
a:focus {
  outline: none;
}
.arrow {
  position: absolute;
  top: 85vh;
  left: 50%;
  transform: translate(-50%, -50%);
  transform: rotate(0deg);
  cursor: pointer;
}
a.arrowLink {
  z-index: 2;
}
.arrow span {
  display: block;
  width: 1.5vw;
  height: 1.5vw;
  border-bottom: 5px solid white;
  border-right: 5px solid white;
  transform: rotate(0deg);
  margin: -10px;
  animation: animate 2s infinite;
}

@keyframes animate {
  0% {
    opacity: 0;
    transform: rotate(45deg) translate(-20px, -20px);
  }
  50% {
    opacity: 1;
  }
  100% {
    opacity: 0;
    transform: rotate(45deg) translate(20px, 20px);
  }
}

.section1 {
  background-image: url("/images/header-bg.jpg");
  background-size: cover;
  height: 90vh;
  z-index: 2;
  border: 5px solid black;
}
.section2 {
  height: 100vh;
  z-index: 2;
  border: 5px solid black;
}
.section3 {
  height: 150vh;
  border: 5px solid black;
}

body {
  margin: 0;
  padding: 0;
  background-color: #2e2e2e;
}

ul.bar {
  display: flex;
  align-items: center;
  color: white;
  list-style-type: none;
  padding: 0;
  margin: 0;
  height: 10vh;
  justify-content: space-between;
  font-family: "Orbitron", sans-serif;
}
.logo {
  font-family: "Rubik Scribble", system-ui;
  font-size: xx-large;
}
li:first-child {
  margin-left: 20px;
  letter-spacing: 5px;
}

li {
  margin-right: 60px;
}

.divider {
  height: 1px;
  width: 100%;
  background-color: white;
}

body::-webkit-scrollbar {
  width: 0.5rem;
}

body::-webkit-scrollbar-track {
  display: none;
}

body::-webkit-scrollbar-thumb {
  background: #4a4a4a;
}
html {
  scroll-behavior: smooth;
}
.toggleButton {
  display: none;
}
.line {
  height: 2px;
  width: 20px;
  background-color: white;
  margin-top: 2px;
  margin-bottom: 2px;
}
@media only screen and (max-width: 800px) {
  .logo {
    font-size: large;
    grid-area: logo;
  }
  a {
    grid-area: links;
  }
  ul.bar {
    height: auto;
    width: auto;
    display: grid;
    grid: "logo logo logo menu";
    justify-content: space-around;
  }
  .links {
    display: none;
  }
  .links.active {
    display: block;
    font-size: small;
  }
  button.toggleButton:active {
    background-color: #2e2e2e;
  }
  .toggleButton {
    margin-left: 40px;
    grid-area: menu;
    border: none;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 30px;
    height: 30px;
    cursor: pointer;
    background-color: transparent;
  }
  li:first-child {
    margin-left: 5px;
    letter-spacing: 2px;
  }
  li {
    margin-right: 0px;
  }
  .title {
    font-size: 24px;
  }
  .titleWrapper {
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: rgb(88, 88, 88);
    width: auto + 1px;
    height: 5vh;
  }
  .section1 {
    background-image: url("/images/header-bg.jpg");
    background-size: cover;
    width: auto;
    height: 90vh;
    z-index: 2;
    border: 2px solid black;
  }
  .section2 {
    height: auto;
    width: auto;
    z-index: 2;
    border: 2px solid black;
  }
  .about {
    padding-top: 5vh;
    padding-bottom: 5vh;
  }
  p {
    font-size: small;
    margin: 0px 30px 0px 30px;
  }
  .section3 {
    height: auto;
    width: auto;
    border: 2px solid black;
  }
  .projectList {
    color: white;
    display: flex;
    flex-direction: column;
    justify-content: center;
    height: 75vh;
  }
  .project1,
  .project2,
  .project3,
  .project4 {
    display: flex;
    justify-content: center;
    align-items: center;
  }
}
@media only screen and (min-width: 800px) and (max-width: 1200px) {
  .title {
    font-size: 36px;
  }
  .titleWrapper {
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: rgb(88, 88, 88);
    width: 40vw;
    height: 7.5vh;
  }
  p {
    font-size: medium;
    margin: 0px 30px 0px 30px;
  }
}
